html,
body {
  margin: 0;
  padding: 0;
  border: 0;
  font-family: "Ubuntu", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 1rem;
  font-weight: 300;
  line-height: 160%;
  -webkit-font-smoothing: antialiased; }

a {
  text-decoration: underline; }
  a:link {
    color: #ED4C67; }
  a:visited {
    color: #ED4C67; }
  a:hover {
    color: #ED4C67; }
  a:active {
    color: #ED4C67; }

.container {
  width: 800px;
  margin: 0 auto; }

@media (max-width: 900px) {
  .container {
    width: 100%;
    margin: 0 auto; } }
