#header {
  background-color: #222f3e;
  color: white; }
  #header .header-container {
    margin: 0 1rem;
    padding: 1rem 0; }
  #header #header-title {
    font-size: 1.7rem;
    margin: 1rem auto;
    font-weight: bold; }
    #header #header-title a {
      color: white;
      text-decoration: none; }
  #header #avatar-img {
    display: inline-block;
    -webkit-border-radius: 25px;
    -moz-border-radius: 25px;
    border-radius: 25px;
    width: 50px;
    height: 50px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
    vertical-align: middle; }
  #header #profile {
    display: inline-block;
    vertical-align: middle;
    padding-left: 1rem;
    font-family: Merriweather, Courier, serif;
    color: lightgrey;
    font-size: 0.9rem; }
  #header #menu {
    float: right;
    display: block;
    height: 50px;
    font-family: Merriweather, Courier, serif;
    font-size: 0.9rem; }
    #header #menu #header-tag {
      display: inline-block;
      margin: auto 1rem; }
    #header #menu #header-about {
      display: inline-block;
      margin: auto 1rem; }
